import { useState, useEffect } from 'react'

export function CookieConsent() {
  const [show, setShow] = useState(false)

  useEffect(() => {
    const consent = localStorage.getItem('cookie-consent')
    if (!consent) {
      setShow(true)
    }
  }, [])

  const accept = () => {
    localStorage.setItem('cookie-consent', 'true')
    setShow(false)
  }

  if (!show) return null

  return (
    <div className="fixed bottom-0 left-0 right-0 z-50 bg-gray-900 p-4 text-white shadow-lg md:p-6">
      <div className="mx-auto flex max-w-container flex-col items-center justify-between gap-4 md:flex-row">
        <div className="text-sm text-gray-300">
           Cookie 
        </div>
        <div className="flex gap-3">
          <button
            onClick={accept}
            className="rounded-full bg-brand px-6 py-2 text-sm font-medium text-white transition-colors hover:bg-blue-600"
          >
            
          </button>
        </div>
      </div>
    </div>
  )
}
