/**
 * PostModal Component
 * 
 *  Modal -  prompt()
 *  QASection  Modal  Focus TrapEscape 
 */

import { useState, useEffect, useRef, useCallback } from 'react';

interface PostModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (content: string) => Promise<void> | void;
  visibility: 'public' | 'private';
  minLength?: number;
  maxLength?: number;
}

export function PostModal({
  isOpen,
  onClose,
  onSubmit,
  visibility,
  minLength = 5,
  maxLength = 500,
}: PostModalProps) {
  const [content, setContent] = useState('');
  const [error, setError] = useState('');
  const [submitting, setSubmitting] = useState(false);
  const dialogRef = useRef<HTMLDivElement | null>(null);
  const textareaRef = useRef<HTMLTextAreaElement | null>(null);
  const restoreFocusRef = useRef<HTMLElement | null>(null);

  const isPrivate = visibility === 'private';
  const title = isPrivate ? '' : '';
  const placeholder = isPrivate 
    ? '...' 
    : '...';

  const reset = useCallback(() => {
    setContent('');
    setError('');
    setSubmitting(false);
  }, []);

  const handleClose = useCallback(() => {
    if (submitting) return;
    reset();
    onClose();
  }, [submitting, reset, onClose]);

  const validate = (): string | null => {
    const trimmed = content.trim();
    if (trimmed.length < minLength) {
      return ` ${minLength} `;
    }
    if (trimmed.length > maxLength) {
      return ` ${maxLength} `;
    }
    return null;
  };

  const handleSubmit = async () => {
    const validationError = validate();
    if (validationError) {
      setError(validationError);
      return;
    }

    setSubmitting(true);
    setError('');

    try {
      await onSubmit(content.trim());
      reset();
      onClose();
    } catch (err) {
      setError(err instanceof Error ? err.message : '');
    } finally {
      setSubmitting(false);
    }
  };

  // Focus Trap
  const getFocusableElements = (container: HTMLElement): HTMLElement[] => {
    const selectors = [
      'button:not([disabled])',
      'textarea:not([disabled])',
      'input:not([disabled])',
      '[tabindex]:not([tabindex="-1"])',
    ].join(',');
    return Array.from(container.querySelectorAll<HTMLElement>(selectors));
  };

  const trapFocus = useCallback((event: KeyboardEvent) => {
    if (event.key !== 'Tab' || !dialogRef.current) return;
    
    const focusable = getFocusableElements(dialogRef.current);
    if (!focusable.length) return;

    const first = focusable[0];
    const last = focusable[focusable.length - 1];
    const active = document.activeElement as HTMLElement | null;

    if (!event.shiftKey && active === last) {
      first?.focus();
      event.preventDefault();
    }
    if (event.shiftKey && active === first) {
      last?.focus();
      event.preventDefault();
    }
  }, []);

  // Escape 
  const handleKeyDown = useCallback((event: KeyboardEvent) => {
    if (event.key === 'Escape' && !submitting) {
      handleClose();
    }
    trapFocus(event);
  }, [submitting, handleClose, trapFocus]);

  //  textarea body
  useEffect(() => {
    if (isOpen) {
      restoreFocusRef.current = document.activeElement as HTMLElement | null;
      document.body.style.overflow = 'hidden';
      //  DOM 
      setTimeout(() => textareaRef.current?.focus(), 10);
    } else {
      document.body.style.overflow = '';
      // 
      if (restoreFocusRef.current?.focus) {
        restoreFocusRef.current.focus();
      }
    }
  }, [isOpen]);

  // 
  useEffect(() => {
    if (!isOpen) return;
    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, handleKeyDown]);

  if (!isOpen) return null;

  const charCount = content.length;
  const isOverLimit = charCount > maxLength;

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4"
      role="dialog"
      aria-modal="true"
      aria-labelledby="post-modal-title"
      onClick={(e) => {
        if (e.target === e.currentTarget) handleClose();
      }}
    >
      <div
        ref={dialogRef}
        className="w-full max-w-md rounded-2xl bg-white shadow-xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header */}
        <div className="flex items-center justify-between border-b border-border-light px-4 py-3">
          <h2 id="post-modal-title" className="flex items-center gap-2 text-base font-bold text-brand-700">
            {isPrivate ? '' : ''} {title}
          </h2>
          <button
            type="button"
            onClick={handleClose}
            disabled={submitting}
            className="rounded-full p-1 text-ink-600 hover:bg-brand/10 hover:text-brand disabled:opacity-50"
            aria-label=""
          >
            
          </button>
        </div>

        {/* Body */}
        <div className="p-4">
          <textarea
            ref={textareaRef}
            value={content}
            onChange={(e) => {
              setContent(e.target.value);
              if (error) setError('');
            }}
            placeholder={placeholder}
            disabled={submitting}
            rows={5}
            className={`w-full resize-none rounded-xl border bg-brand/3 p-3 text-sm text-ink-900 placeholder:text-ink-400 focus:border-brand focus:outline-none focus:ring-2 focus:ring-brand/20 disabled:opacity-50 ${
              error ? 'border-red-400' : 'border-border-light'
            }`}
            aria-describedby={error ? 'post-modal-error' : undefined}
            aria-invalid={!!error}
          />
          
          {/*  */}
          <div className="mt-2 flex items-center justify-between text-xs">
            <span className={isOverLimit ? 'text-red-500' : 'text-ink-500'}>
              {charCount} / {maxLength} 
            </span>
            {isPrivate && (
              <span className="text-brand-600"> </span>
            )}
          </div>

          {/*  */}
          {error && (
            <p id="post-modal-error" className="mt-2 text-sm text-red-500" role="alert">
               {error}
            </p>
          )}
        </div>

        {/* Footer */}
        <div className="flex justify-end gap-2 border-t border-border-light px-4 py-3">
          <button
            type="button"
            onClick={handleClose}
            disabled={submitting}
            className="rounded-lg border border-border-light px-4 py-2 text-sm font-medium text-ink-600 hover:bg-brand/5 disabled:opacity-50"
          >
            
          </button>
          <button
            type="button"
            onClick={handleSubmit}
            disabled={submitting || isOverLimit}
            className="rounded-lg bg-brand px-4 py-2 text-sm font-bold text-white hover:bg-brand-700 disabled:opacity-50"
            aria-busy={submitting}
          >
            {submitting ? '...' : ''}
          </button>
        </div>
      </div>
    </div>
  );
}
